import React, { useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import '../assets/css/LeftNav.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMeteor, faIdCard, faShieldAlt, faDiceD20, faCog, faDragon } from '@fortawesome/free-solid-svg-icons';
import EventEmitter from '../services/EventEmitter';
import backpackIcon from "../assets/img/backpack.png";
// import raceIcon from "../assets/img/orc-head.png";
import encounterIcon from "../assets/img/sword-clash.png";

export default function LeftNav() {
  const [openNav, setOpenNav] = useState("");
  const location = useLocation();

  const closeActiveView = () => {
    EventEmitter.dispatch("closeActiveView");
  }

  const openOnTouch = () => {
    if(openNav !== "open"){
      setOpenNav("open");
    } else {
      setOpenNav("");
    }
  }

  return (
    <div id="leftNav" className={openNav}>
      <div className="gradIcon" onTouchEnd={() => openOnTouch()}>
        <FontAwesomeIcon icon={faDiceD20} className="smallIcon" />
      </div>
      <Link onClick={e => closeActiveView()} to="/spell-overview" className={location.pathname === "/spell-overview" ? "menuItemActiv" : ""}>
        <FontAwesomeIcon icon={faMeteor} />
      </Link>
      <Link onClick={e => closeActiveView()} to="/item-overview" className={location.pathname === "/item-overview" ? "menuItemActiv" : ""}>
        <FontAwesomeIcon icon={faShieldAlt} />
      </Link>
      <Link onClick={e => closeActiveView()} to="/gear-overview" className={location.pathname === "/gear-overview" ? "menuItemActiv" : ""}>
        <img alt="" src={backpackIcon} style={{ width: '20px', marginTop: '10px', marginRight: '5px', float: 'left' }} />
      </Link>
      <Link onClick={e => closeActiveView()} to="/char-overview" className={location.pathname === "/char-overview" ? "menuItemActiv" : ""}>
        <FontAwesomeIcon icon={faIdCard} />
      </Link>
      <Link onClick={e => closeActiveView()} to="/monster-overview" className={location.pathname === "/monster-overview" ? "menuItemActiv" : ""}>
        <FontAwesomeIcon icon={faDragon} />
      </Link>
      <Link onClick={e => closeActiveView()} to="/encounter" className={location.pathname === "/encounter" ? "menuItemActiv" : ""}>
        <img alt="" src={encounterIcon} style={{ width: '20px', marginTop: '10px', marginRight: '5px', float: 'left' }} />
      </Link>
      <Link onClick={e => closeActiveView()} to="/options" className={location.pathname === "/options" ? "menuItemActiv" : ""}>
        <FontAwesomeIcon icon={faCog} />
      </Link>
      <svg width="0" height="0">
        <radialGradient id="rgg" r="150%" cx="30%" cy="107%">
          <stop stopColor="#350069" offset="0" />
          <stop stopColor="#B973FF" offset="0.9" />
        </radialGradient>
      </svg>
    </div>
  )
}
