import { MyAppDatabase } from "./MyDatabase";

const db = new MyAppDatabase();
let searchSpellQuery;

export function reciveSpell(id, callback) {
  db.open()
    .then(function () {
      db.spells.where("id").equals(id)
        .then((spell) => {
          callback(spell);
        })
        .finally(function () {
          db.close();
        });
    })
    .finally(function () {
      db.close();
    });
}

export function reciveSpellByName(name, callback) {
  db.open()
    .then(function () {
      db.spells.where("name").equalsIgnoreCase(name)
        .then((spell) => {
          callback(spell);
        })
        .finally(function () {
          db.close();
        });
    })
    .finally(function () {
      db.close();
    });
}

export function reciveAllSpells(callback) {
  db.open()
    .then(function () {
      db.spells.toCollection().sortBy('name', function (array) {
        callback(array);
      })
    })
    .finally(function () {
      db.close();
    });
}

export function reciveSpells(query, callback) {

  if (query !== null) {
    searchSpellQuery = query.query;
  }

  db.open()
    .then(function () {
      console.time("sortSpells")
      db.spells
        .filter(spell => {
          let schoolbool = true;
          if (searchSpellQuery.school !== null && searchSpellQuery.school.length !== 0) {
            schoolbool = false;
            searchSpellQuery.school.map(school => {
              if (spell.school === school.value) schoolbool = true;
            });
          }
          let levelbool = true;
          if (searchSpellQuery.level !== null && searchSpellQuery.level.length !== 0) {
            levelbool = false;
            searchSpellQuery.level.map(level => {
              if (spell.level === level.value) levelbool = true;
            });
          }
          return (
            (searchSpellQuery.name !== undefined && spell.name.includes(searchSpellQuery.name))
            && (searchSpellQuery.time !== undefined && spell.time.includes(searchSpellQuery.time))
            && (searchSpellQuery.range !== undefined && spell.range.includes(searchSpellQuery.range))
            && (searchSpellQuery.duration !== undefined && spell.duration.includes(searchSpellQuery.duration))
            && (searchSpellQuery.components !== undefined && spell.components.includes(searchSpellQuery.components))
            && (searchSpellQuery.text !== undefined && spell.text.includes(searchSpellQuery.text))
            && (searchSpellQuery.classes !== undefined && spell.classes.includes(searchSpellQuery.classes))
            && (searchSpellQuery.sources !== undefined && spell.sources.includes(searchSpellQuery.sources))
            && ((searchSpellQuery.ritual && spell.ritual === 1) || (!searchSpellQuery.ritual))
            && levelbool
            && schoolbool
          );
        })
        .sortBy('name', function (array) {
          console.timeEnd("sortSpells")
          callback(array);
        })
    })
    .finally(function () {
      db.close();
    });
}

export function reciveSpellCount(attribute, value, callback) {
  db.open()
    .then(function () {
      db.spells.where(attribute).equalsIgnoreCase(value).count(count => {
        callback(count);
      })
    })
    .finally(function () {
      db.close();
    });
}
export function reciveSpellFiles(callback) {
  db.open()
    .then(function () {
      db.spells.orderBy("filename").uniqueKeys(function (array) {
        if (array.length > 0) {
          reciveSpellFileCount("filename", array, 0, function (result) {
            callback(result);
          })
        } else {
          callback([]);
        }
      })
    })
    .finally(function () {
      db.close();
    });
}
export function reciveSpellFileCount(attribute, array, value, callback) {
  reciveSpellCount(attribute, array[value], function (result) {
    array[value] = [array[value], result];
    value++;
    if (array.length > value) {
      reciveSpellFileCount(attribute, array, value, callback)
    } else {
      callback(array);
    }
  });
}

export function reciveAttributeSelection(attribute, callback) {
  db.open()
    .then(function () {
      db.spells.orderBy(attribute).uniqueKeys(function (array) {
        callback(array);
      })
    })
    .finally(function () {
      db.close();
    });
}

export function saveSpell(spell) {
  db.open()
    .then(function () {
      db.spells.update(spell.id, spell);
    })
    .finally(function () {
      db.close();
    });
}

export function saveNewSpell(spell) {
  db.open()
    .then(function () {
      db.spells.put(spell);
    })
    .finally(function () {
      db.close();
    });
}

export function saveNewSpells(spells, filename, callback) {
  let spellImportLength = Object.keys(spells).length;
  let spellImported = 0;
  db.open()
    .then(function () {
      spells.map(spell => {
        db.spells.put({
          name: spell.name !== undefined ? spell.name : "",
          classes: spell.classes !== undefined ? spell.classes : "",
          sources: spell.sources !== undefined ? spell.sources : "",
          level: spell.level !== undefined ? spell.level : 0,
          school: spell.school !== undefined ? spell.school : "",
          time: spell.time !== undefined ? spell.time : "",
          range: spell.range !== undefined ? spell.range : "",
          components: spell.components !== undefined ? spell.components : "",
          duration: spell.duration !== undefined ? spell.duration : "",
          ritual: spell.ritual !== undefined ? spell.ritual : 0,
          text: spell.text !== undefined ? spell.text : "",
          pic: spell.pic !== undefined ? spell.pic : "",
          filename: filename,
        }).then(() => {
          spellImported++;
          callback({ now: spellImported, full: spellImportLength, name: spell.name });
        });
        return true;
      });
    })
    .finally(function () {
      db.close();
    });
}

export function saveNewSpellsFromJson(charId, spells, callback) {
  db.open();
  spells.map(spell => {
    db.spells.where({ "name": spell.name })
      .first((foundSpell) => {
        console.log("Foundspell:", foundSpell);
        if (foundSpell === undefined) {
          db.spells.put({
            name: spell.name !== undefined ? spell.name : "",
            classes: spell.classes !== undefined ? spell.classes : "",
            sources: spell.sources !== undefined ? spell.sources : "",
            level: spell.level !== undefined ? spell.level : 0,
            school: spell.school !== undefined ? spell.school : "",
            time: spell.time !== undefined ? spell.time : "",
            range: spell.range !== undefined ? spell.range : "",
            components: spell.components !== undefined ? spell.components : "",
            duration: spell.duration !== undefined ? spell.duration : "",
            ritual: spell.ritual !== undefined ? spell.ritual : 0,
            text: spell.text !== undefined ? spell.text : "",
            pic: spell.pic !== undefined ? spell.pic : ""
          }).then((key) => {
            db.chars_spells.put({
              char_id: charId,
              spell_id: key,
              prepared: spell.prepared
            }).then(() => {
              callback();
            });
          });
        } else {
          db.chars_spells.put({
            char_id: charId,
            spell_id: foundSpell.id,
            prepared: spell.prepared
          }).then(() => {
            callback();
          });
        }
      }).catch(error => {
        console.error(error.stack || error);
      });
  });
}

export function deleteSpell(spell) {
  db.open()
    .then(function () {
      db.spells.where('id').equals(spell.id).delete();
    })
    .finally(function () {
      db.close();
    });
}

export function deleteAllSpells() {
  db.open()
    .then(function () {
      db.spells.clear();
    })
    .finally(function () {
      db.close();
    });
}

export function addSpellToChar(char, spell, callback) {
  db.open()
    .then(function () {
      db.chars_spells.put({ "char_id": char, "spell_id": spell.id, "prepared": false });
    })
    .finally(function () {
      db.close();
    });
}
