export function saveCharSpells(spells) {
    db.open()
        .then(function () {
            spells.forEach(spell => {
                db.chars_spells.update(spell.charSpellId, { "prepared": spell.prepared });
            });
        })
        .finally(function () {
            db.close();
        });
}

