export function deleteCharSpell(char, spell, callback) {
    db.open()
        .then(function () {
            db.chars_spells.where({ char_id: char, spell_id: spell.id }).delete().then(callback());
        })
}
export function deleteCharSpells(char) {
    db.open()
        .then(function () {
            db.chars_spells.where({ char_id: char }).delete().then(function (deleteCount) {
                console.log( "Deleted " + deleteCount + " objects");
            });
        })
}

export function deleteCharItem(char, item, callback) {
    db.open()
        .then(function () {
            db.chars_items.where({ id: item.charItemId }).delete().then(callback());
        })
}
export function deleteCharItems(char) {
    db.open()
        .then(function () {
            db.chars_items.where({ char_id: char }).delete();
        })
}

