
import { MyAppDatabase } from "./MyDatabase";

const db = new MyAppDatabase();
let searchItemQuery;

export function reciveAllItems(callback) {
    db.open()
        .then(function () {
            db.items.toCollection().sortBy('name', function (array) {
                callback(array);
            })
        })
        .finally(function () {
            db.close();
        });
}
export function reciveItemByName(name, callback) {
    db.open()
        .then(function () {
            db.items.where("name").equalsIgnoreCase(name)
                .then((item) => {
                    callback(item);
                })
                .finally(function () {
                    db.close();
                });
        })
        .finally(function () {
            db.close();
        });
}

export function reciveItems(query, callback) {
    if (query !== null) {
        searchItemQuery = query.query;
    }

    db.open()
        .then(function () {
            console.time("sortItems")
            db.items
                .filter(item => {
                    let raritybool = true;
                    if (searchItemQuery.rarity !== null && searchItemQuery.rarity.length !== 0) {
                        raritybool = false;
                        searchItemQuery.rarity.map(rarity => {
                            if (item.rarity === rarity.value) raritybool = true;
                        });
                    }
                    let typebool = true;
                    if (searchItemQuery.type !== null && searchItemQuery.type.length !== 0) {
                        typebool = false;
                        searchItemQuery.type.map(type => {
                            if (item.type === type.value) typebool = true;
                        });
                    }
                    return (
                        (searchItemQuery.name !== undefined && item.name.includes(searchItemQuery.name))
                        && (searchItemQuery.description !== undefined && item.description.includes(searchItemQuery.description))
                        && (searchItemQuery.sources !== undefined && item.sources.includes(searchItemQuery.sources))
                        && ((searchItemQuery.attunment && item.attunment === 1) || (!searchItemQuery.attunment))
                        && raritybool
                        && typebool
                    );
                })
                .sortBy('name', function (array) {
                    console.timeEnd("sortItems")
                    callback(array);
                })
        })
        .finally(function () {
            db.close();
        });
}

export function reciveItemCount(attribute, value, callback) {
    db.open()
        .then(function () {
            db.items.where(attribute).equalsIgnoreCase(value).count(count => {
                callback(count);
            })
        })
        .finally(function () {
            db.close();
        });
}
export function reciveItemFiles(callback) {
    db.open()
        .then(function () {
            db.items.orderBy("filename").uniqueKeys(function (array) {
                if (array.length > 0) {
                    reciveItemFileCount("filename", array, 0, function (result) {
                        callback(result);
                    })
                } else {
                    callback([]);
                }
            })
        })
        .finally(function () {
            db.close();
        });
}
export function reciveItemFileCount(attribute, array, value, callback) {
    reciveItemCount(attribute, array[value], function (result) {
        array[value] = [array[value], result];
        value++;
        if (array.length > value) {
            reciveItemFileCount(attribute, array, value, callback)
        } else {
            callback(array);
        }
    });
}

export function reciveAttributeSelection(attribute, callback) {
    db.open()
        .then(function () {
            db.items.orderBy(attribute).uniqueKeys(function (array) {
                callback(array);
            })
        })
        .finally(function () {
            db.close();
        });
}

export function saveItem(item) {
    db.open()
        .then(function () {
            db.items.update(item.id, item);
        })
        .finally(function () {
            db.close();
        });
}

export function saveNewItem(item) {
    db.open()
        .then(function () {
            db.items.put(item);
        })
        .finally(function () {
            db.close();
        });
}

export function saveNewItems(items, filename, callback) {
    
    let itemImportLength = Object.keys(items).length;
    let itemImported = 0;
    db.open()
        .then(function () {
            items.map(item => {
                db.items.put({
                    name: item.name !== undefined ? item.name : "",
                    sources: item.sources !== undefined ? item.sources : "",
                    pic: item.pic !== undefined ? item.pic : "",
                    description: item.description !== undefined ? item.description : "",
                    rarity: item.rarity !== undefined ? item.rarity : "",
                    type: item.type !== undefined ? item.type : "",
                    attunment: item.attunment !== undefined ? item.attunment : 0,
                    filename: filename,
                }).then(() => {
                    itemImported++;
                    callback({ now: itemImported, full: itemImportLength, name: item.name });
                });
            });
        })
        .finally(function () {
            db.close();
        });
}

export function saveNewItemFromJson(item, callback) {

}

export function deleteItem(item) {
    db.open()
        .then(function () {
            db.items.where('id').equals(item.id).delete();
        })
        .finally(function () {
            db.close();
        });
}

export function deleteAllItems() {
    db.open()
        .then(function () {
            db.items.clear();
        })
        .finally(function () {
            db.close();
        });
};

export function addItemToChar(char, item, callback) {
    db.open()
        .then(function () {
            db.chars_items.put({ "char_id": char, "item_id": item.id, "amount": 1, "equiped": false, "attuned": false, "damage": "", "properties": "" });
        })
        .finally(function () {
            db.close();
        });
}